import React from "react";
import { PageLayout } from "../../components/Layouts/PageLayout";
import { ShortAbout } from "../../components/ShortAbout";
import { TitleText } from "../../components/TitleText";
import { FlexWrapper } from "../../components/Wrappers/FlexWrapper";
import { useQuery } from "../../styles/breakpoints";
import { COLORS } from "../../styles/colors";
import { routes } from "../../assets/route";
import { RouteCard } from "../../components/RouteCard";
import { ServiceBlock } from "../home/sections/ServiceBlock";
import { services } from "../home/home";
import { DefaultButton } from "../../components/DefaultButton";

export const Cayaks = () => {
  const { isTablet } = useQuery();
  return (
    <PageLayout
      heroText="PLAUKIMAS BAIDARMIS"
      heroImg="./images/baidares_v2.png"
      heroBtnText="REZERVUOKITE VIET"
      contPadding="85px 0 135px 0"
    >
      <ShortAbout
        imageSrcLeft="./images/leaf-left-big.svg"
        imageSrcRight="./images/leaf-left-big.svg"
        width="150px"
        padding="24px"
        transform="rotate(180deg)"
        alignLeftDoodle="flex-end"
        alignRightDoodle="flex-start"
        heroText="Turbt viena populiariausi iltojo sezono pramog, kuri visad buna
        kupina nuotyki ir puiki patiri. Esame sikr gausiai vandens
        apdovanotame krate, todl galime pasilyti istorikai bei vizualiai
        turting kelion, kurioje, inome, kad patirsite kur kas daugiau."
      />
      <FlexWrapper
        padding={isTablet ? "50px 10% 20px 10%" : "16px 24px"}
        flexDirection="column"
      >
        <TitleText color={COLORS.gray} align="left">
          MARRUTAI
        </TitleText>
        <FlexWrapper flexWrap="wrap" gap="24px" margin="30px 0 0 0">
          {routes.map(({ routeName, route, img, length }, index) => {
            return (
              <RouteCard
                key={`card-${index}`}
                routeFt={img}
                routeName={routeName}
                route={route}
                length={length}
              />
            );
          })}
        </FlexWrapper>
        <FlexWrapper padding="50px" justifyContent="center">
          <DefaultButton to="/rezervacija">REZERVUOKITE</DefaultButton>
        </FlexWrapper>
        <TitleText color={COLORS.gray} align="left" margin="40px 0 25px 0">
          PAPILDOMAI SILOME
        </TitleText>
        <FlexWrapper flexDirection={isTablet ? "row" : "column"} gap="24px">
          {services.map(({ title, description, img, id }, index) => {
            if (id !== "baidares") {
              return (
                <ServiceBlock
                  key={index}
                  title={title}
                  img={img}
                  id={id}
                  direction="column"
                >
                  {description}
                </ServiceBlock>
              );
            }
          })}
        </FlexWrapper>
      </FlexWrapper>
    </PageLayout>
  );
};
